
import axios from 'axios';
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "billDetails",
data() {
return {
getdetails:[],
getMoneyLog:[]
}
},
components: {
Box, Group, Cell , XHeader
},
created() {

},
mounted() {
var _this=this;
this.$axios.post('/api/Partner/getMoneyDetail',"data="+JSON.stringify({id:_this.$route.query.id})).then(
function(res){

if(res.code==1){
_this.getdetails=res.result;
_this.getMoneyLog=res.result.getMoneyLog;
}else{
mui.toast(res.msg)
}
}
)
},
methods: {

}
}



